import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { connect } from 'react-redux';
import { push } from 'react-router-redux';
import PropTypes from 'prop-types';
import styles from './App.css.js';


class App extends Component {
  static propTypes = {
    dispatch: PropTypes.func.isRequired,
        location: PropTypes.shape({
            pathname: PropTypes.string
        })
  };
  
  static defaultProps = {
    location: undefined
  };

  goToIndex = () => {
    this.props.dispatch(push("/"))
  }

  render() {
    return (
      <div className="App">
        <div style={styles.sectionWrapper}>

          <div>
            <nav style={styles.appNavbar}>
            <div style={styles.appNavbarContent}>
              <div style={styles.appNavbarInner}>
                <a style={styles.navItem} onClick={this.goToIndex}>Workmoose</a>  
                {/* <a style={styles.navItem} onClick={this.goToVideoSubmission} >Submit a Video</a> */}
              </div>
            </div>
            </nav>
          </div>
          </div>
        <div>
            {this.props.children}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    location: state.routing.location
  };
}

export default connect(mapStateToProps)(App);
export { App as AppNotConnected };


