import React, {Component} from 'react';
import Radium from 'radium';

import { SectionWrapper } from '../../Components';
import Footer from '../../Containers/';

import styles from './Product.css';
import typography from '../../Styles/Typography.css';

import rembrandtImg from './images/rembrandt.png';
import appStoreImg from './images/app_store.svg';
import playstoreImg from './images/google-play-badge.png';

class ProductView extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            showPrivacy: this.props.privacy
         };

    }

    goToStoreLink = (link) => {
        var url = '';
        if (link === 'apple') {
            url = 'https://itunes.apple.com/nl/app/rembrandt-college/id936635620?l=en&mt=8'
        } else if (link === 'google') {
            url = 'https://play.google.com/store/apps/details?id=rickhutten.rembrandtcollege'
        }

        window.open(url, '_blank')
    }

    render() {
        return (

            <SectionWrapper>
                <div style={styles.pageWrapper}>
                    <div style={styles.productHeader}>
                        <span style={[styles.productTitle, typography.sparseText, typography.bannerText, typography.thinText, typography.capsText]}>Rembrandt College</span>

                        <div style={styles.productLogoContainer}>
                            <div style={styles.productLogo}>
                                <img 
                                    style={styles.productLogoImage}
                                    src={rembrandtImg}
                                />
                            </div>
                        </div>
                    </div>

                    {!this.state.showPrivacy && (
                        <div>
                            <div style={styles.productAnecdoteContainer}>
                                <span style={[styles.productAnecdote, typography.titleText, typography.largerLineHeight, typography.thinText]}>
                                    Stay up to date with the happenings at the Rembrandt College with this app. The newsfeed is packed with fun reports about student projects, excursions and other exciting stuff.
                                </span>
                            
                            </div>

                                <div style={styles.appStoresContainer}>
                                    <img 
                                        src={appStoreImg} 
                                        onerror="this.onerror=null; this.src='image.png'"
                                        style={[styles.storeIcon, styles.appStore]}
                                        onClick={()=> this.goToStoreLink('apple')}
                                    />

                                    <img
                                        src={playstoreImg}
                                        style={[styles.storeIcon, styles.playStore]}
                                        onClick={() => this.goToStoreLink('google')}
                                    />
                                
                                </div>
                        </div>
                        
                    )}

                    
                    {this.state.privacy && (
                        <div style={styles.termsContainer}>
                            <div style={styles.termsHeader}>
                                <span style={[typography.headingText, typography.thinText]}>Privacy Policy</span>
                                <span style={typography.thinText}>more...</span>
                                    
                            </div>
                        </div>
                    )}
                    
                </div>
                <div>
                    <Footer/>
                </div>
            </SectionWrapper>
               
        );
    }
}

export default Radium(ProductView);


