import React, { Component } from 'react';
import Radium from 'radium';
import styles from './HomeView.css';
import typography from '../../Styles/Typography.css';

import { connect } from 'react-redux';
import { push } from 'react-router-redux';
import PropTypes from 'prop-types';

// components
import Footer from '../../Containers/';
// images
import swiftImg from './images/icons8-swift_filled.png';
import pythonImg from './images/icons8-python_filled.png';
import javascriptImg from './images/icons8-javascript_filled.png';
import mathImg from './images/icons8-scatter_plot_filled.png';
import cppImg from './images/icons8-c_plus_plus_filled.png';
import aiImg from './images/icons8-artificial_intelligence_filled.png';
import hardwareImg from './images/icons8-integrated_circuit_filled.png';

import physicsImg from './images/icons8-physics.png';
import codeImg from './images/icons8-code.png';
import cogImg from './images/icons8-settings3.png';

import jaapImg from './images/jaap2.png';
import thomImg from './images/Thom.jpg';
import rickImg from './images/rick.jpeg';
// import amsterdamImg from './images/amsterdam-coutout.png';
import mooseImg from './images/moose.png'

class HomeView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cycle: -1,
            bannerTexts: [
                'We build.',
                'We think.',
                'We question.',
                'We solve.',
                'We compute.',
                'We create.',
                'We moose.',
            ],
            currentBannerText: '',
        };


        this.updateBannerText = this.updateBannerText.bind(this);
        this.updateTheText = this.updateTheText.bind(this);
        this.typeWord = this.typeWord.bind(this);
        this.setTextTimeout = this.setTextTimeout.bind(this);
        this.backspaceWord = this.backspaceWord.bind(this);
        this.renderLanguage = this.renderLanguage.bind(this);
        this.clickLink = this.clickLink.bind(this);
    }

    updateTheText = (character) => {
        let newText = this.state.currentBannerText + character;

        this.setState({
            currentBannerText: newText,
        });

    };

    typeWord(newWord){
        return new Promise((resolve, reject) => {
            let splittedWord = newWord.split('');
    
            let updateTheText = (character) => {
                let newText = this.state.currentBannerText + character;
        
                this.setState({
                    currentBannerText: newText,
                });
        
            };
    
            var count = 0;
            splittedWord.map((character) => {
                count += 1;
                let randTime = Math.floor(Math.random() * 51 );
                let baseTime = count*150
                return setTimeout(() => updateTheText(character), baseTime + randTime);
            });
            return setTimeout(() =>{resolve()}, 4000)
        });
            
    }

    backspaceWord() {
        return new Promise((resolve, reject) => {
            let splittedText = this.state.currentBannerText.split('');

            let updateTheText = (shorterString) => {
                this.setState({
                    currentBannerText: shorterString,
                });
        
            };
            let count = 1;
            splittedText.map(() => {
                count += 1;
                let randTime = Math.floor(Math.random() * 51 );
                let baseTime = count*100
                let shorterString = this.state.currentBannerText.slice(0, -count);
                return setTimeout(() => updateTheText(shorterString), baseTime + randTime);
            });
            return setTimeout(() =>{resolve()}, 1000)
        });
    }

    updateBannerText() {
        let cycle = this.state.cycle;
        let nextCycle = cycle + 1 >= this.state.bannerTexts.length ? 0 : cycle + 1;

        let newText = this.state.bannerTexts[nextCycle];

        this.typeWord(newText).then(() => {

            this.backspaceWord().then(()=>{
                this.setState({
                    cycle: nextCycle,
                    currentBannerText: '',
                });
                this.setTextTimeout();
            });
        });

        
    }

    setTextTimeout() {
        var textTimeout = setTimeout(()=> this.updateBannerText(), 1000);
    }

    componentWillMount() {

    }

    componentDidMount() {
        this.setTextTimeout();
    }

    renderLanguage(language) {
        return (
            <div style={styles.languageCell}>
                <img 
                    src={language.image}
                    style={styles.languageImage}
                    title={language.label} />
            </div>
        )
    }

    renderTeamMember(member){
        return (
            <div style={styles.teammemberCard}>
                <div style={styles.teammemberImageContainer}>
                    <img 
                        src={member.image}
                        style={styles.teammemberImage}/>
                </div>
                <span style={[styles.teammemberName, typography.titleText, typography.mediumText]}>{member.name}</span>
            </div>
        )
    }

    clickLink(link){
        this.props.dispatch(push('/rembrandt-college'));
    }

    render() {
        let languages = [{
            label: 'swift',
            image: swiftImg,
        },
        {
            label: 'python',
            image: pythonImg,
        },
        {
            label: 'javascript',
            image: javascriptImg,
        },
        {
            label: 'machine learning',
            image: mathImg,
        },
        {
            label: 'c++',
            image: cppImg,
        },
        {
            label: 'ai',
            image: aiImg,
        }, 
        {
            label: 'hardware',
            image: hardwareImg,
        }];

        let teammembers = [
            {
                name: 'Jaap',
                image: jaapImg,
            },
            {
                name: 'Thom',
                image: thomImg,
            },
            {
                name: 'Rick',
                image: rickImg,
            }
        ]
        return (
            <div style={styles.homeWrapper}>
                {/* landing banner */}
                <div style={styles.sectionWrapper}>
                    <div style={styles.landingContainer}>
                        <div>
                            <div style={styles.landingHeadingContainer}>
                                <span id="typewriter" style={[styles.landingHeading, typography.hugeText, typography.thinText, typography.sparseText]}>{this.state.currentBannerText}</span>
                            </div>
                        </div>
                        <div style={styles.landingImageContainer}>
                            <img
                                style={styles.landingImage}
                                src={mooseImg}
                            />
                        </div>
                    </div>
                </div>

                {/* vision well */}
                <div style={styles.sectionWrapper}>
                    <div style={styles.languageWell}>
                        <span style={[styles.languageHeader, typography.titleText, typography.thinText, typography.sparseText, typography.capsText]}>We are looking forward to the future </span>
                        
                        <div style={styles.messageContainer}>
                            <div style={styles.messageContainerRow}>
                                <div style={styles.messageContainerRowItem}>
                                    <img
                                        style={styles.messageContainerRowItemImg}
                                        src={physicsImg}
                                    />
                                   <p>We bring physics into the mix.</p>
                                </div>
                                <div style={styles.messageContainerRowItem}>
                                    <img
                                        style={styles.messageContainerRowItemImg}
                                        src={codeImg}
                                    />
                                   <p>We build everything code.</p>
                                </div>
                                <div style={styles.messageContainerRowItem}>
                                    <img
                                        style={styles.messageContainerRowItemImg}
                                        src={cogImg}
                                    />
                                   <p>We bring ideas into the physical world.</p>
                                    
                                </div>
            
                            </div>
                        </div>
                    </div>
                </div>

                

                {/* team section */}
                <div style={styles.sectionWrapper}>
                    <div style={styles.teamContainer}>
                        <div style={styles.teammemberWrapper}>
                            {teammembers.map(member => this.renderTeamMember(member))}
                        </div>
                    </div>
                </div>

              {/* language well */}
              <div style={styles.sectionWrapper}>
                    <div style={styles.languageWell}>
                        <span style={[styles.languageHeader, typography.titleText, typography.thinText, typography.sparseText, typography.capsText]}>We are well-versed</span>
                        
                        <div style={styles.languageContainer}>
                            {languages.map(language => this.renderLanguage(language))}
                        </div>
                    </div>
                </div>

                <div style={styles.sectionWrapper}>
                    
                    <Footer />
                    
                    {/* <div style={styles.footer}>
                        <div style={[styles.footerBody, typography.thinText]}>
                            <div style={styles.footerBodyImage}>
                                <img 
                                    src={amsterdamImg}
                                    style={styles.footerImage}
                                />                
                            </div>
                            <div style={styles.footerBodyContent}>
                                <span style={[typography.subheadingText, typography.thinText, typography.sparseText, {display: 'block', marginBottom: '24px'}]}>CONTACT</span>
                                <span style={{display: 'block', marginBottom: '12px'}}>
                                    Our hideout is in Amsterdam!
                                </span>
                                <span style={{display: 'block', marginBottom: '32px'}}>
                                    Drop us a line at <span>info@workmoose.nl</span>
                                </span>
                                <span style={[typography.subheadingText, typography.thinText, typography.sparseText, {display: 'block', marginBottom: '24px'}]}>HANDY LINKS</span>
                                <span style={{display: 'block', marginBottom: '32px'}}>
                                    <a onClick={()=> this.clickLink('rembrandt')} >Rembrandt-College</a>
                                </span>
                            </div>
                        </div>
                        <div style={styles.footerBottom}>
                            <span style={[styles.footerInner, typography.thinText]}>
                                <span>&copy; 2018</span> <span style={{marginLeft: '32px'}}>Workmoose</span>
                            </span>
                        </div>
                    </div> */}

                </div>

            </div>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
        location: state.routing.location
    };
}

export default connect(mapStateToProps)(Radium(HomeView));
export { HomeView as HomeViewNotConnected };


