import React from 'react';

const SectionWrapper = (props) => {

    const styles = {
        sectionWrapper: {
            margin: '0 auto 0 auto',
            maxWidth: '1440px',
        }
    }


    return (
        <div style={styles.sectionWrapper}>
            {props.children}
        </div>
    )
}

export default SectionWrapper;


