import React from 'react';
import { Route, Switch } from 'react-router';

import HomeView from './Views/HomeView/HomeView';
import ProductView from './Views/ProductDetailView/Product';
// import withTracker from './Utilities/WithTracker';

const RembrandtPage = (props) => {
    return (
        <ProductView
            privacy={false}
            {...props}
        />
    );
}

export default(
    <Switch>
        <Route exact path="/" component={HomeView} />
        <Route path="/rembrandt-college/privacy" render={RembrandtPage}/>
        <Route path="/rembrandt-college" component={ProductView} />

        {/* should be 404 default page */}
        <Route path="*" component={HomeView} />
    </Switch>
);



