import colors from '../../Styles/Colors.css';
import { width } from 'window-size';


const styles = {
    pageWrapper: {
        backgroundColor: 'white',
    },
    productHeader:{
        display: 'flex',
        flexDirection: 'column',
        padding: '144px 0px 84px',
        minHeight: '250px',
        backgroundColor: colors.bgColor,
    },
    productLogoContainer:{
        backgroundColor: colors.colorDark,
        height: '48px',
        width: '100%',
        position: 'relative',
    },
    productLogo:{
        position: 'absolute',
        top: '-103px',
        padding: '32px',
        backgroundColor: colors.colorDark,
        width: '242px',
        height: '242px',
        margin: '0 auto 0 76px',
        boxSizing: 'border-box',
        borderRadius: '50%',
        '@media screen and (max-width: 599px)':{
            margin: '0 auto 0 auto',
        },
    },
    productLogoImage:{
        maxWidth: '180px',
        height: '180px',
        borderRadius: '50%'
    },
    productTitle:{
        marginLeft: '350px',
        padding: '24px 12px',
        '@media screen and (max-width: 599px)':{
            marginLeft: '24px',
            marginBottom: '184px'
        },
        '@media screen and (min-width: 769px)':{
            marginLeft: '500px',
        },

    },
    productAnecdoteContainer:{
        display: 'flex',
        margin: '64px 0 0 0',
        padding: '88px 12px',
        backgroundColor: 'white'
    },
    productAnecdote: {
        margin: 'auto',
        textAlign: 'center',
        maxWidth: '400px',
        borderLeft: '4px solid',
        borderColor: colors.colorDark,
        padding: '24px'
    },
    appStoresContainer:{
        display: 'flex',
        padding: '64px 12px',
        alignItems: 'center',
        justifyContent: 'center',
        marginBottom: '64px'
    },
    storeIcon: {
        cursor: 'pointer'
    },
    appStore:{
        margin: 'auto 88px auto auto',
        height: '60px'
    },
    playStore:{
        margin: 'auto auto auto 88px',
        height: '80px'
    },
    termsContainer: {
        margin: '64px auto',
        maxWidth: '800px',
        backgroundColor: colors.bgColor,
        padding: '24px',
        // boxShadow: '0px 0px 16px 0px rgba(0, 0, 0, 0.2)',
    },
    termsHeader:{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
}

export default styles;


