module.exports = {
    captionText: {
        fontSize: '12px',
        lineHeight: 'normal',
    },
    bodyText: {
        fontSize: '14px',
        lineHeight: 'normal',
    },
    subheadingText: {
        fontSize: '16px',
        lineHeight: '18px',
    },
    titleText:{
        fontSize: '20px',
        lineHeight: '24px',
    },
    headingText : {
        fontSize: '32px',
        lineHeight: '34px',
    },
    bannerText : {
        fontSize: '42px',
        lineHeight: '1.3',
    },
    hugeText: {
        fontSize: '64px',
        lineHeight: '1.3',
    },
    strongText :{
        fontWeight: 'bold',
    },
    mediumText: {
        fontWeight: '400',
    },
    regularText:{
        fontWeight: 'normal',
    },
    thinText: {
        fontWeight: '100',
    },
    sparseText:{
        letterSpacing: '2px'
    },
    doubleLineHeight:{
        lineHeight: '2em',
    },
    largerLineHeight: {
        lineHeight: '1.5em',
    },
    capsText: {
        textTransform: 'uppercase'
    }
}


