import typography from './Styles/Typography.css.js';
import colors from './Styles/Colors.css.js';

const styles = {
    sectionWrapper:{
        margin: '0 auto 0 auto',
        maxWidth: '1440px',
    },
    appNavbar: {
        height: '72px',
        backgroundColor: colors.bgColor,
        borderBottom: '2px solid',
        borderColor: colors.bgColorMedium
      },
      appNavbarContent:{
        display: 'flex',
        justifyContent: 'center',
        height: '100%'
      },
     appNavbarInner: {
        width: '100%',
        maxWidth: '1440px',
        position: 'relative',
        padding: '24px 32px',
        height: '100%',
        display: 'flex',
        flexDirection: 'row',
        boxSizing: 'border-box',
        alignItems: 'center',
        justifyContent: 'flex-end'
      },
      navItem: {
          margin: 'auto auto auto 24px',
          cursor: 'pointer',
          fontSize: '14px',
          fontWeight: 'bold',
        //   color: colors.darkBlue,
          letterSpacing: '1.5px',
          textTransform: 'uppercase',
      }
}

export default styles;


