import React, { Component } from 'react';
import Radium from 'radium';
import styles from './Footer.css';
import typography from '../../Styles/Typography.css';

import { connect } from 'react-redux';
import { push } from 'react-router-redux';

import amsterdamImg from './images/amsterdam-coutout.png';


class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = {  };

        this.clickLink = this.clickLink.bind(this);

    }



    clickLink(link){
        if (link == 'home') {
            this.props.dispatch(push('/'));
        } else if (link == 'rembrandt') {
            this.props.dispatch(push('/rembrandt-college'));
        }
        window.scrollTo(0,0);
    }
    render() {
        return (
            <div style={styles.footer}>
                        <div style={[styles.footerBody, typography.thinText]}>
                            <div style={styles.footerBodyImage}>
                                <img
                                    src={amsterdamImg}
                                    style={styles.footerImage}
                                />
                            </div>
                            <div style={styles.footerBodyContent}>
                                <span style={[typography.subheadingText, typography.thinText, typography.sparseText, {display: 'block', marginBottom: '24px'}]}>CONTACT</span>
                                <span style={{display: 'block', marginBottom: '12px'}}>
                                    Our hideout is in Amsterdam!
                                </span>
                                <span style={{display: 'block', marginBottom: '32px'}}>
                                    Drop us a line at <span>info@workmoose.nl</span>
                                </span>
                                <span style={[typography.subheadingText, typography.thinText, typography.sparseText, {display: 'block', marginBottom: '24px'}]}>PROJECTS</span>
                                {/* <span style={{display: 'block', marginBottom: '12px'}}>
                                    <a onClick={()=> this.clickLink('home')} >Home</a>
                                </span> */}

                                <span style={{display: 'block', marginBottom: '32px'}}>
                                    <a onClick={()=> this.clickLink('rembrandt')} >Rembrandt-College</a>
                                </span>
                            </div>
                        </div>
                        <div style={styles.footerBottom}>
                            <span style={[styles.footerInner, typography.thinText]}>
                                <span>&copy; 2020</span> <span style={{marginLeft: '32px'}}>Workmoose</span>
                            </span>
                        </div>
                    </div>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
        location: state.routing.location
    };
}

export default connect(mapStateToProps)(Radium(Footer));
export { Footer as FooterNotConnected };



