import colors from '../../Styles/Colors.css';
import breakpoints from '../../Styles/Breakpoints.css';

const styles = {
    homeWrapper:{
        // display: 'flex',
        width: '100%',
        height: '100%',
        backgroundColor: 'white',
    },
    sectionWrapper:{
        margin: '0 auto 0 auto',
        maxWidth: '1440px',
    },
    landingContainer:{
        display: 'flex',
        flexDirection: 'row',
        height: '600px',
        maxWidth: '100%',
        backgroundColor: colors.bgColor,
        '@media screen and (max-width: 768px)': {
            flexDirection: 'column',
            height: '500px',

        }
    },
    landingHeadingContainer: {
        display: 'flex',
        flexDirection: 'column',
        margin: '0 auto auto 0',
    },
    landingImageContainer:{
        display: 'flex',
        margin: 'auto 0 0 auto',
        padding: '0 64px 0 0',
        '@media screen and (max-width: 767px)': {
            padding: '0 16px 0 0',
        },
    },
    landingImage:{
        maxWidth: '300px',
        marginTop: 'auto'
    },
    landingHeading:{
        margin: '88px auto auto 64px',
        borderColor: 'black',
        borderRight: '2px solid',
        '@media screen and (max-width: 767px)': {
            margin: '64px auto auto 16px',
            fontSize: '48px'
        },
    },
    languageWell:{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start',
        // height: '268px',
        backgroundColor: colors.bgColorMedium,
        boxShadow: 'inset 0 100px 200px -200px rgba(0,0,0,0.4)',
        '@media screen and (max-width: 767px)': {
          boxShadow: 'inset 0 0px 50px -20px rgba(0,0,0,0.4)',
        },
    },
    languageHeader:{
        margin: '96px auto 32px auto',
        padding: '0 12px',
        textAlign: 'center',
    },
    languageContainer:{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        flexWrap: 'wrap',
        padding: '12px 0 120px'
    },
    messageContainer:{
        textAlign: 'center',
        // maxWidth: 400,
        justifyContent: 'center',
        padding: '12px 0 80px',
        margin: '0 auto',
    },
    messageContainerRow:{
        margin:'o auto',
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'stretch',
        '@media screen and (max-width: 767px)': {
            flexDirection: 'column',
        },
    },
    messageContainerRowItem:{
        margin: '0 12px auto',
        display: 'flex',
        maxWidth: '220px',
        height: '100%',
        flexDirection: 'column',
        alignItems: 'center',
        padding: '12px 12px',
    },
    messageContainerRowItemImg:{
        width: '50px',
        height: '50px',
        margin: 'auto'
    },
    languageCell:{
        width: '64px',
        height: '64px',
        overflow: 'hidden',
        display: 'flex',
        border: '2px solid',
        borderColor: colors.colorDark,
        borderRadius: '80%',
        margin: 'auto 16px 8px 16px',
    },
    languageImage:{
        width: '50px',
        height: '50px',
        margin: 'auto'
    },
    teamContainer:{
        position: 'relative',
        backgroundColor: colors.colorDark,
        height: '144px',
        padding: '36px 12px',
        '@media screen and (max-width: 767px)': {
            height: '100%',
        },
    },
    teammemberWrapper:{
        position: 'relative',
        top: '-66px',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        '@media screen and (max-width: 767px)': {
            flexDirection: 'column',
            top: 0,
            paddingBottom: '32px',
        },

    },
    teammemberCard: {
        backgroundColor: 'white',
        margin: '0 32px 0 32px',
        // width: '150px',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        borderRadius: '3px',
        padding: '16px 16px 32px 16px',
        boxShadow: '0px 0px 16px 0px rgba(0, 0, 0, 0.2)',
        zIndex: 2,
        '@media screen and (max-width: 767px)': {
            margin: '32px auto 0',
        },
    },
    teammemberName:{},
    teammemberImageContainer: {
        width: '168px',
        height: '168px',
        borderRadius: '50%',
        marginBottom: '32px',
        overflow: 'hidden',
    },
    teammemberImage:{
        maxWidth: '100%',
        maxHeight:'100%'
    },
    footer: {
        position: 'relative',
        height: '100%',
        backgroundColor: colors.darkGrey,
        color: 'white',
    },
    footerBody:{
        display: 'flex',
        height: '100%',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '120px 12px',
        '@media screen and (max-width: 767px)': {
            flexDirection: 'column'
        },
    },
    footerBodyContent: {
        paddingTop: '72px',
        flex: 1,
    },
    footerBodyImage:{
        flex: 1,
        position: 'relative',
        display: 'flex',
        '@media screen and (max-width: 767px)': {
            order: 2
        },
    },
    footerBottom:{
        display: 'flex',
        height: '72px',
        backgroundColor: colors.darkGrey,
        borderTop: '3px solid',
        borderColor: colors.grassGreen,
    },
    footerInner: {
        margin: 'auto',
    },
    footerImage:{
        maxWidth: '400px',
        maxHeight: '400px',
        margin: 'auto',
        '@media screen and (max-width: 767px)': {
            maxWidth: '200px',
            maxHeight: '200px',
        },
        '@media only screen and (min-device-width : 768px) and (max-device-width : 1024px)': {
            maxWidth: '300px',
            maxHeight: '300px',
        },
    },
    dotsCanvas: {
        position: 'absolute',
        top: '0px',
        height: '100%',
        width: '100%',
        backgroundColor: 'grey'
    }
}

export default styles;



