import colors from '../../Styles/Colors.css';

const styles = {
    footer: {
        position: 'relative',
        height: '100%',
        backgroundColor: colors.darkGrey,
        color: 'white',
    },
    footerBody:{
        display: 'flex',
        height: '100%',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '120px 12px',
        '@media screen and (max-width: 767px)': {
            flexDirection: 'column'
        },
    },
    footerBodyContent: {
        paddingTop: '72px',
        flex: 1,
    },
    footerBodyImage:{
        flex: 1,
        position: 'relative',
        display: 'flex',
        '@media screen and (max-width: 767px)': {
            order: 2
        },
    },
    footerBottom:{
        display: 'flex',
        margin: '0 auto',
        height: '72px',
        // maxWidth: '1080px',
        backgroundColor: colors.darkGrey,
        borderTop: '2px solid',
        borderColor: colors.grassGreen,
    },
    footerInner: {
        margin: 'auto',
    },
    footerImage:{
        maxWidth: '300px',
        maxHeight: '300px',
        margin: 'auto',
        '@media screen and (max-width: 767px)': {
            maxWidth: '200px',
            maxHeight: '200px',
        },
        '@media only screen and (min-device-width : 768px) and (max-device-width : 1024px)': {
            maxWidth: '300px',
            maxHeight: '300px',
        },
    },
}
export default styles;


